import { Modal, Backdrop, Fade, Box, Typography, Grid } from "@mui/material"
import React from "react"

const style = {
    position: 'absolute' as 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 800,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
}

function RulesModal({open, handleClose}: {open: boolean, handleClose: any}) {
    return (
        <Modal
            open={open}
            onClose={handleClose}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
            closeAfterTransition
            slots={{ backdrop: Backdrop }}
            slotProps={{
            backdrop: {
                timeout: 500,
            },
            }}
        >
            <Fade in={open}>
                <Box sx={style}>
                <Grid container justifyContent="center" spacing={5}>
                    <Grid item xs={12}>
                        <Typography variant="h3" align="center">Rgles du jeu</Typography>
                    </Grid>
                    <Grid item xs={12}>
                        <Typography variant="h4" paragraph>But du jeu:</Typography>
                        <Typography variant="body1" paragraph component="ol">
                            En rejoignant une room, vous rejoindrez d'autres joueurs dont l'objectif est d'apprendre  catgoriser des images qui vous seront prsentes successivement.
                            La catgorie de chaque image est fixe selon une rgle secrte cre par le crateur de la room qui fait office de superviseur.
                            Votre objectif est de deviner la catgorie de chaque image et de voter pour la catgorie que vous pensez tre la bonne. A chaque vote, vous gagnerez ou perdrez des points en fonction de la justesse de votre prdiction.
                            Le but du jeu est d'obtenir le plus de points possible.
                        </Typography>
                        <Typography variant="h4" paragraph>Objectif pdagogique:</Typography>
                        <Typography variant="body1" paragraph component="ol">
                            Le jeu a pour objectif de faire dcouvrir aux joueurs le fonctionnement de l'apprentissage supervis.
                            En effet, chaque joueur joue le rle d'une IA qui doit apprendre  catgoriser des images selon une rgle secrte.
                            L'amlioration des prdictions passe donc uniquement par l'observation des rsultats des prdictions prcdentes.
                            Le jeu permet galement de mettre en avant l'importance de la qualit des donnes d'entranement et des labels.
                        </Typography>
                        <Typography variant="caption">
                            Dvelopp dans le cadre du mmoire HEP pour le secondaire II de Vincent Grtler
                        </Typography>
                    </Grid>
                    </Grid>
                </Box>
            </Fade>
        </Modal>
    )
}

export default RulesModal